<template>
    <form @submit.prevent="handleSubmit">
        <h1 class="text--center">Editar prerfil</h1>

        <div class="row">
            <div class="col-xs-6 row">
                <Input
                    class="col-xs-6"
                    v-model="form.teacher.first_name"
                    :value="teacher.first_name"
                    label="Nombres"
                />

                <Input
                    class="col-xs-6"
                    v-model="form.teacher.last_name"
                    :value="teacher.last_name"
                    label="Apellidos"
                />
            </div>
        </div>

        <Button type="submit">Guardar</Button>

        <pre>{{ teacher }}</pre>
    </form>
</template>

<script>
export default {
    props: ['teacher'],

    data: () => ({
        form: {
            teacher: {},
            address: {},
            levels: [],
            categories: [],
        },

        loading: false,
        errros: new ErrorBag(),
    }),

    methods: {
        handleSubmit() {
            if (this.loading) return

            this.loading = true
            this.errros.clear()

            try {
            } catch (error) {
                console.log(error.response || error)
                if (error.response) {
                    window.scrollTo({ top: 0, behavior: 'smooth' })
                    const { errors } = error.response.data
                    this.errors.set(errors || {})
                }
            } finally {
                this.loading = false
            }
        },
    },
}
</script>
